      },
      textareaValue: ""
    };
  },
  methods: {
    checkStr() {
      let len = this.textareaValue.replace(/[^\x00-\xff]/g, "__").length;
      if (len > 1000) {
        alert("字数不能超过500个字");
        return;
      } else {
        console.log("pass");
      }
    },

    
