<style lang="less" scoped>
@m: 750/375;
/* 主体区域 */

.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  // border: 1px solid transparent;
  font-size: 14px * @m;
  height: 595px * @m;
  overflow: auto;
}

/* 头部产品信息 */

.container .aboutGoods {
  margin: 30px * @m 0;
}

.container .aboutGoods .goods {
  height: 108px * @m;
  background: #ffffff;
  // box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.8);
  // border-radius: 6px * @m;
  margin-bottom: 30px * @m;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.container .aboutGoods .goods img {
  width: 162px * @m;
  height: 108px * @m;
  border-radius: 6px * @m;
  margin-right: 16px * @m;
}
.container .aboutGoods .goods .info {
  padding: 3px* @m 0;
}
.container .aboutGoods .goods .info p {
