  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 16px * @m;
}

.container .aboutGoods .goods .info span {
  display: block;
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 6px * @m;
}

/* 申请理由 */

.lable {
  color: #bcbcbc;
  margin-bottom: 15px * @m;
}

#applyReason {
  resize: none;
  width: 100%;
  font-size: 18px * @m;
  height: auto;
  border: 0;
}

/* 按钮 */

.nextBtn {
  position: relative;
  bottom: 0;
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: 100px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 40px * @m;
  text-align: center;
  margin: 0px auto;
  font-size: 16px * @m;
  margin-top: 10px * @m;
}
</style>



