
import Vue from 'vue'
import Router from 'vue-router'


// 承载
import applyForTest from '../pages/applyForTest.vue'
// 主页
import applyForTestIndex from '../views/apply-for-test.vue'

Vue.use(Router)

const router =  new Router({
  routes: [{
      path: '/',
      name: 'applyForTestIndex',
      component: applyForTestIndex
    }
  ]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#applyForTest',
  router,
  components: {
    applyForTest
  },
  template: '<applyForTest/>'
})



